/* src/Components/SignInForm/SignInForm.css */
.signin-form-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.signin-form-container {
    background: white;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 400px;
}
  
.close-button {
    background: none;
    border: none;
    font-size: 2.3em;
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
}

.signin-heading {
    font-size: 1.5rem;
    color: lightgray;
    text-align: center;
    margin-bottom: 20px;
}
  
form {
    display: flex;
    flex-direction: column;
}
  
label {
    margin-bottom: 10px;
    font-size: 0.9rem;
    color: black;
    text-align: left;
    font-weight: normal;
}
  
input {
    margin-bottom: 20px;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
}
  
button {
    background-color: #ff4081;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
  
button:hover {
    background-color: #fdb3b3;
}
